        }),
        methods: {
            getProviderForId(id) {
                switch (id) {
                    case firebase.auth.GoogleAuthProvider.PROVIDER_ID:
                        return new firebase.auth.GoogleAuthProvider();
                    case firebase.auth.FacebookAuthProvider.PROVIDER_ID:
                        return new firebase.auth.FacebookAuthProvider();
                    case firebase.auth.GithubAuthProvider.PROVIDER_ID:
                        return new firebase.auth.GithubAuthProvider();
                }

            },
            writeUserData(userId, name, email, imageUrl) {
                var database = firebase.database();

                database.ref('users/' + userId).set({
                    username: name,
                    email: email,
                    profile_picture: imageUrl
                });
            },
            snsLogin(who) {
                var me = this
                var app = this.getComponent('App')

                if (who == 'google') {
                    var provider = new firebase.auth.GoogleAuthProvider();
                    provider.addScope('profile');
                    provider.addScope('email')

                    firebase.auth().signInWithPopup(provider).then(function (result) {

                        var token = result.credential.accessToken;
                        var user = result.additionalUserInfo.profile;

                        localStorage.setItem("author", user.name)
                        localStorage.setItem("email", user.email)
                        localStorage.setItem("picture", user.picture)
                        localStorage.setItem("accessToken", token)

                        app.loginDialog = false
                        // app.successLogin = true
                        me.$EventBus.$emit('login',token)

                        //firebase DB input
                        me.writeUserData(user.id, user.name, user.email, user.picture)
                    }).catch(function (error) {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        alert("[" + errorCode + "] " + errorMessage)
                    });


                } else if (who == 'facebook') {
                    console.log('facebook');
                    var provider = new firebase.auth.FacebookAuthProvider();
                    firebase.auth().signInWithPopup(provider).then(function (result) {
                        var token = result.credential.accessToken;
                        var user = result.user;
                        console.log(result)

                        app.loginDialog = false
                    }).catch(function (error) {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        alert("[" + errorCode + "] " + errorMessage)
                    });

                } else if (who == 'github') {
                    console.log('git');
                    var provider = new firebase.auth.GithubAuthProvider();
                    provider.addScope('repo');

                    firebase.auth().signInWithPopup(provider).then(function (result) {
                        // This gives you a GitHub Access Token. You can use it to access the GitHub API.
                        var token = result.credential.accessToken;
                        // The signed-in user info.
                        var user = result.user;

                        console.log(result)

                        // localStorage.setItem("author", user.name)
                        // localStorage.setItem("email", user.email)
                        // localStorage.setItem("picture",user.picture)
                        localStorage.setItem("accessToken", token)

                        // app.loginDialog = false
                    }).catch(function (error) {

                        if (error.code === 'auth/account-exists-with-different-credential') {
                            firebase.auth().fetchSignInMethodsForEmail(error.email).then(function(providers) {
                                if (providers[0] == 'google.com') {
                                    var provider = new firebase.auth.GoogleAuthProvider();
                                    // popup   signInWithRedirect
                                    firebase.auth().signInWithRedirect(provider).then(function(result) {
                                        firebase.auth().signInWithCredential(result.credential).then(user => {
                                            console.log(user.linkWithCredential)
                                            user.linkWithCredential(error.credential)
                                        }).catch(function(e) {
                                        })
                                    }).catch(function(e) {
                                    })
                                }
                            })
                        } else {
                            alert('   .  . ErrorCode : ' + error.code)
                        }

                        // console.log(error)
                        // if (error.code === 'auth/account-exists-with-different-credential') {
                        //     var pendingCred = error.credential;
                        //     var email = error.email;
                        //
                        //     firebase.auth().fetchSignInMethodsForEmail(email).then(function (methods) {
                        //         var provider = me.getProviderForId(methods[0]);
                        //         console.log(provider)
                        //         firebase.auth().signInWithPopup(provider).then(function (result) {
                        //             result.user.linkAndRetrieveDataWithCredential(pendingCred).then(function (usercred) {
                        //                 // goToApp();
                        //             });
                        //         });
                        //     });
                        //
                        // }

                    });
                }


            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },

        }
    }
</script>

